.main-profil{ display: flex;
              height: auto;
              padding: 50px 30px 0px 30px;
              width: 100%;
              flex-direction: column;
              flex-wrap: wrap;
              align-content: flex-start;
              justify-content: flex-start;
              align-items: flex-start;
              font-size:.7em;}

.profil1{font-family: 'ApercuProMono';
         color:#ffe0dc;
         width:100%;
         margin-bottom:15px;
         display: flex;
         justify-content: center;}

.profil2{display: flex;
         width:100%;
         height: auto;
         font-family: 'ApercuProMono';
         color:#ffe0dc;
         justify-content: center;}

.photo-profil{max-width: 50px;
              max-height:50px;
              float:left;
              margin-right:15px;}

.fw-bold{font-weight: bold;
         -webkit-text-stroke-width:.4px;}

.color-red{color:red;}

.profil1, .profil2{opacity:0;
                   transition:.8s opacity ease-in;}

                   .profil-opacity-off{opacity:1;}