.audio-container{display:flex;
                 align-items: center;
                 justify-content: center;
                 bottom: 10px;
                 flex-direction: column;
                 position: fixed;
                 right: 10px;
                 margin: 0px;
                 z-index:98;
                }
                #audio-visualiser{
                    height: 35px;
                    margin: 5px 5px;
                    width: 45px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }

                #audio-visualiser .audio-wire{
                    background-color: #ffe0dc;
                    margin: 0 1.2px;
                    padding: 0 .5px;
                    height: 15px;
                    z-index:0;
                    --animdur: .6s;
                    --animn: animMUSIC;
                    --animtf:cubic-bezier(.57,.21,.69,1.25);
                    --animic: infinite;
                    --animdir: alternate;
                    will-change: height;}

                    .bg-audio-wire{background-color: #0b0a0ade;
                                   height: 60%;
                                   position: absolute;
                                   width: 70%;
                                   z-index: -1;
                                   border-radius: .4px;
                                   border: .1px solid #cbcbcb;
                                   -webkit-filter: drop-shadow( 0px 0px .5px rgb(155, 155, 155));
                                   filter: drop-shadow( 0px 0px .5px rgb(155, 155, 155));}

                                   #audio-visualiser .audio-wire-1{
                                    --animdel:50ms;}

                                    #audio-visualiser .audio-wire-2{
                                        --animdel:150ms;}

                                        #audio-visualiser .audio-wire-3{
                                            --animdel:300ms;}

                                            #audio-visualiser .audio-wire-4{
                                                --animdel:450ms;}

                                                #audio-visualiser .audio-wire-5{
                                                    --animdel:300ms;}

                                                    #audio-visualiser .audio-wire-6{
                                                        --animdel:150ms;}

                                                        #audio-visualiser .audio-wire-7{
                                                            --animdel:50ms;}

                                    @keyframes animMUSIC {
                                        to{height:30%;}
                                        from{height:10%;}
                                    }

.jouer, .pause{ align-content: center;
                align-items: center;
                display: flex;
                flex-direction: column;
                height: 35px;
                justify-content: center;
                margin: 5px;
                padding: 10px;
                position: absolute;
                width: 35px;
                z-index: 1;
                stroke: #cbcbcb;
                stroke-width: 2.3px;
                stroke-linecap: round;
                stroke-linejoin: round;
                display:none;
                opacity:0%;}

.a{fill:#7d938a;}
.b{fill:#ffe0dc;}

  [data-animation] {
    animation: var(--animn, none) var(--animdur, 0s) var(--animtf, linear) var(--animdel, 0s) var(--animic, infinite) var(--animdir, alternate) var(--animfm, none) var(--animps, running);
  }