.menu_main3{
        height: 100%;
        width: 100%;
        position: absolute;
        padding: 0px;
        z-index: 99;
        justify-content: center;
        align-content: center;
        background-color: black;
        flex-direction: column;
        z-index:5;
        overflow:hidden;}

  .menu3{
    display: flex;
    font-family: 'TIMMONS NY 2.0';
    font-style: normal;
    font-weight: 100;
    font-size:5em;
    justify-content: center;
    margin: 0px;
    flex-direction: column;
    align-content: center;
    align-items: center;
    z-index:6;}

    .menu-txt-flex{align-content: center;
                   height: 40vh;
                   padding: 0px;
                   position: absolute;
                   width: 40vw;
                   z-index: 5;
                   overflow: hidden;
                   background-color:#7d938a;
                   display: flex;
                   align-items: center;
                   justify-content: center;
                   flex-direction: column;
                   align-self: center;
                   border: .01em solid #1f1e1e;
                   animation: MENUDESKTOP 2s cubic-bezier(0.075, 0.82, 0.165, 1);
                   animation-iteration-count:1;}

    .menu-txt-overflow{align-self: center;
                       color: #ffe0dc;
                       font-family: ApercuProMono;
                       font-size: 1em;
                       justify-content: center;
                       margin: 1px;
                       opacity: 100%;
                       width: max-content;
                       height: max-content;}

                       .cat3{-webkit-text-fill-color: white;
                             -webkit-text-stroke-width: 1px;}

                             .cat4{color:#7d938a;}

                             @keyframes MENUDESKTOP{
                                from{height: 40vh;width: 40vw;}
                                to{height: 100vh;width: 100vw;}}
                        
.ow-15{animation: WAVE 1s ease-in-out infinite;
      animation-delay: 50ms;}

      @keyframes WAVE{
        from{transform:translateX(0px);}
        to{transform:translateX(168px);}}







    